
import { getBannerList, getActiveProductList } from '@/api/home.js'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import activeNav from '@/components/activeNav.vue'
import CountDown from 'vue2-countdown'
import noData from '@/components/noData.vue'
export default {
name: 'homeIndex',
components: {
Swiper,
Tabbar,
TabbarItem,
activeNav,
CountDown,
noData
},
data () {
return {
bannerlist: [],
skulist: {
data: {
skulist: []
}
}
}
},
methods: {
getbannerList () {
const that = this
const postData = {
location: 'cate',
activetype: 1
}
getBannerList(postData)
.then(res => {
if (res.data.errorcode === 0) {
var list = []
res.data.data.bannerlist.map(item => {
let listurl = {}
listurl.img = item.picurl
listurl.url = item.target
listurl.title = item.target
list.push(listurl)
})
that.bannerlist = list
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
},
activetime (row) {
if (row == undefined) { return }
var t = new Date(row.replace(/-/g, '/'))
t = Date.parse(t)
return t
},
nowdate () {
var date = new Date() 
var str = date.getTime()
return str
},
ActiveProduct () {
const postData = {
activetype: '1'
}
getActiveProductList(postData)
.then(res => {
if (res.data.errorcode === 0) {
if (res.data.data.skulist && res.data.data.skulist.length > 0) {
this.skulist = res.data
} else {
this.nolist = true
}
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
},
clickmore (id,index,page) {
const postData = {
activetype: '1',
activeid:id,
page: page+1,
}
getActiveProductList(postData)
.then(res => {
if (res.data.errorcode === 0) {
if (res.data.data.skulist && res.data.data.skulist.length > 0) {
for(var i=0;i<res.data.data.skulist[0].skulist.length;i++)
{
this.skulist.data.skulist[index].skulist.push(res.data.data.skulist[0].skulist[i])
}
this.skulist.data.skulist[index].nownum = res.data.data.skulist[0].nownum
} else {
this.nolist = true
}
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
}
},

created () {
this.getbannerList()
this.ActiveProduct()
}

}



